<template>
  <article class="home">

    <header class="header">

      <img class="header-img" src="../assets/img/master-3.png" alt="Аватарка">

      <h1 class="header-h1">Палина Гончарова</h1>

      <p class="header-text">Фотохудожник</p>

    </header>

    <Main :homeImg="trending"/>

    <footer class="footer">

      <a href="111" class="footer-icon">
        <svg class="icon-social"><use xlink:href="../assets/fonts/sprite.svg#vk"></use></svg>
      </a>

      <a href="222" class="footer-icon">
        <svg class="icon-social"><use xlink:href="../assets/fonts/sprite.svg#instagram"></use></svg>
      </a>

      <a href="333" class="footer-icon">
        <svg class="icon-social"><use xlink:href="../assets/fonts/sprite.svg#facebook"></use></svg>
      </a>

      <a href="444" class="footer-icon">
        <svg class="icon-social"><use xlink:href="../assets/fonts/sprite.svg#google-plus"></use></svg>
      </a>

    </footer>

  </article>

</template>

<script>

import Main from '../components/Main.vue'
import homeImg from '../data/home-img.json'

export default {
  name: 'home',
  components: { Main },
  data () {
    return {
      trending: homeImg.filter(a => [1, 2, 3, 4, 5, 6].includes(a.id))
    }
  },

  beforeMount () {
    if (!this.$route.params.id) return

    const rootEl = document.documentElement
    const { top, left } = this.$route.params.position
    const offsetLeft = window.innerWidth / 100 * 10
    const offsetTop = 100
    const x = -(left - offsetLeft)
    const y = -(top - offsetTop)

    rootEl.style.setProperty('--translate-tile', `translate(${x}px, ${y}px)`)
  }

}
</script>

