<template>

  <div class="photo">
    <div class="photo-img">
      <img :src="require(`../assets/img/home-image/${photoId.image}`)" :alt="photoId.alt">
    </div>

    <h2 class="photo-h2">{{ photoId.name }}</h2>

    <Carousel :gallery="galleryId"/>

  </div>

</template>

<script>

import Carousel from '../components/Carousel.vue'

import gallery from '../data/gallery.json'

export default {

  components: { Carousel },

  data () {
    return {
      galleryId: gallery[this.$route.params.id].pictures
    }
  },

  props: {
    photoId: Object
  },

  beforeMount () {
    const rootEl = document.documentElement
    const { left, top } = this.$route.params.position
    const offsetLeft = window.innerWidth / 100 * 10
    const offsetTop = 100
    const x = left - offsetLeft
    const y = top - offsetTop

    scroll(0, 0)
    rootEl.style.setProperty('--translate-header-image', `translate(${x}px, ${y}px)`)
  }
}

</script>

<style lang="sass" scoped>

.photo
  padding: 0 10px 220px

.photo-img
  position: relative
  max-width: 390px
  margin: 0 10%
  outline: 2px solid #B19F5D
  transform-origin: left top
  will-change: transform
  backface-visibility: hidden
  animation: move-image 0.9s var(--easing-smooth)
  &:before
    content: ''
    position: absolute
    top: 0
    left: 0
    width: 92.5vw
    height: 700px
    transform: translate(-7vw, 100px)
    background: radial-gradient(#557143, #0E2C12)
    opacity: 1
    z-index: -1
    animation: photo-image 1s var(--easing-smooth)
  &:after
    content: ''
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0
    background-color: #fff
    z-index: -1
    box-shadow: 0px 25px 50px -5px rgba(0,0,0,0.7)

@keyframes move-image
  from
    transform: var(--translate-header-image)

@keyframes photo-image
  from
    transform: translate(0, 0)
    width: 100%
    height: 100%

.photo-h2
  position: relative
  text-align: center
  font: 4rem "yandex"
  color: #0D100F
  animation: photo-h2-animation 1.2s var(--easing-smooth)

@keyframes photo-h2-animation
  from
    transform: scale(0, 0)

@media only screen and (max-width : 992px)
  .photo-img
    &:before
      width: 91.5vw

  .photo-h2
    font: 3rem "yandex"

@media only screen and (max-width : 768px)
  .photo
    padding: 0 10px 170px

  .photo-img
    &:before
      height: 650px

@media only screen and (max-width : 480px)
  .photo
    padding: 0 10px 40vw

  .photo-img
    &:before
      width: 90vw
      height: 130vw

  .photo-h2
    font: 2.5rem "yandex"
</style>
