<template>

<div class="swiper-box">

  <div class="swiper-container">

    <div id="lightgallery" class="swiper-wrapper">

      <a class="swiper-slide" :href="createUrl(item.image)" v-for="item in gallery" :key="item.id" tabIndex="-1">

        <img :src="require(`../assets/img/small/${item.image}`)" :alt="item.alt">

      </a>

    </div>

  </div>

</div>

</template>

<script>

import 'swiper/css/swiper.min.css'

import Swiper from 'swiper/js/swiper.min.js'

import 'lightgallery.js/dist/css/lightgallery.min.css'

import 'lightgallery.js/dist/js/lightgallery.min.js'

export default {

  props: {
    gallery: Array
  },

  methods: {
    createUrl (name) {
      return require(`../assets/img/${name}`)
    }
  },

  mounted () {
    this.mySwiper = new Swiper('.swiper-container', {
      centeredSlides: true,
      slidesPerView: 'auto',
      // slidesOffsetBefore: '30px',
      loop: true,
      effect: 'coverflow',
      keyboard: {
        enabled: true
      },
      grabCursor: true,
      coverflowEffect: {
        rotate: 0,
        stretch: 100,
        depth: 250,
        modifier: 1,
        slideShadows: true
      }
    })

    window.lightGallery(document.getElementById('lightgallery'), {
      mode: 'lg-slide'
    })
  }
}

</script>

<style lang="sass" scoped>

.swiper-box
  width: 100%
  display: flex
  padding: 0 5vw

.swiper-container
  display: inline-block
  text-align: center
  max-width: 1350px
  margin: 30px auto
  animation: swiper-animation 0.9s var(--easing-smooth)

@keyframes swiper-animation
  from
    transform: scale(0, 0)

.swiper-slide,
.swiper-slide-duplicate
  max-width: 400px
  pointer-events: none

.swiper-slide-active,
.swiper-slide-duplicate-active
  pointer-events: auto
  cursor: zoom-in

</style>
