<template>
  <div id="app">
    <transition>
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app',

  data () {
    return {
      tilePosition: {},
      homeId: null
    }
  },

  created () {
    this.$root.$on('gotoPhoto', (position, homeId) => {
      this.tilePosition = position
      this.homeId = homeId

      this.$router.push({
        name: 'photo',
        params: {
          id: homeId,
          position: position
        }
      })
    })

    this.$root.$on('gotoHome', () => {
      this.$router.push({
        name: 'home',
        params: {
          id: this.homeId,
          position: this.tilePosition
        }
      })
    })
  }
}
</script>
