<template>
  <div class="photo-box">

    <button class="photo-button button" tabindex="1" @click="$root.$emit('gotoHome')">
      <svg class="photo-icon"><use xlink:href="../assets/fonts/sprite.svg#cross"></use></svg>
    </button>

    <PhotoItem :photoId="photoId"/>

  </div>
</template>

<script>

import PhotoItem from '../components/PhotoItem.vue'
import homeImg from '../data/home-img.json'

export default {
  name: 'photo',

  components: { PhotoItem },

  data () {
    return {
      homeId: Number(this.$route.params.id)
    }
  },

  computed: {
    photoId: function () {
      return homeImg.find(a => this.homeId === a.id)
    }
  }

}

</script>

