<template>

  <main class="main">

    <div class="main-wrap">

      <button class="main-button button" v-for="item in homeImg" :key="item.id" :data-id="item.id"  @click="handleClick" :class="{'tile--slide-in': item.id === $route.params.id}">

        <div class="main-wrapper">

          <img class="main-img" :data-id="item.id" :src="createUrl(item.image)" :alt="item.alt">

          <h2 class="main-h2">{{ item.name }}</h2>

        </div>

      </button>

    </div>

  </main>

</template>

<script>

export default {
  name: 'Main',
  props: {
    homeImg: Array
  },
  methods: {
    createUrl (name) {
      return require(`../assets/img/home-image/${name}`)
    },

    handleClick: function (e) {
      const el = e.target
      const homeId = Number(el.dataset.id)
      const { x, y } = el.getBoundingClientRect()
      const position = { left: x, top: y }
      this.$root.$emit('gotoPhoto', position, homeId)
    }

  }
}
</script>

