.main-button
  position: relative
  max-width: 390px
  margin: 10px
  border: 0
  outline: 2px solid #B19F5D
  transition: .4s
  backface-visibility: hidden
  animation: tile-scale-up 1s var(--easing-smooth)
  &:hover,
  &:focus
    .main-img
      transform: scale(1.05,1.05)
    .main-h2
      transform: translateY(-40px)

@keyframes tile-scale-up
  from
    opacity: 0
    transform: scale(0.75)

.main-wrapper
  position: relative
  overflow: hidden

.main-h2
  position: absolute
  left: 0
  right: 0
  bottom: -40px
  margin: 0
  font: 2rem "yandex"
  text-align: center
  background-color: rgba(177, 159, 93, 0.6)
  transition: .4s

.main-img
  transition: .4s

.tile--slide-in
  position: relative
  transform-origin: left top
  will-change: transform
  backface-visibility: hidden
  animation: tile-move 1.2s var(--easing-smooth)
  &:before
    content: ''
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    transform: translate(0, 0)
    background: radial-gradient(#557143, #0E2C12)
    animation: photo-hide 1s var(--easing-smooth)

